.todo {
  width: 90%;
}
.cont_img {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  background-color: #011627;
  cursor: pointer;
}

.cont_img img {
  width: 90%;
  height: 300px;
  margin-left: 10px;
}
.cont_info {
  width: 100%;
  height: 100%;
  justify-content: center;
}

.cont_info p {
  width: 90%;
  text-align: justify;
  background-color: #011627;
  color: white;
  cursor: pointer;
}

.h1 {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.h1 h1 {
  width: 90%;
  display: flex;
  justify-content: left;
  margin: 10px;
}

.Proyectos {
  width: 100%;
  height: 100%;
  align-items: flex-end;
  margin-left: 10%;
}
.Proyectos img {
  width: 350px;
  height: 300px;
  justify-content: left;
  cursor: pointer;
}
.Proyectos p {
  width: 90%;
  text-align: justify;
  background-color: #011627;
  color: white;
  margin: 10px;
}

.Datos {
  width: 80%;
  height: 100%;
  align-items: flex-end;
  margin-left: 10%;
}

.Datos h3 {
  width: 90%;
  display: flex;
  justify-content: left;
  margin-top: 10px;
}

.Datos img {
  justify-content: left;
  cursor: pointer;
  margin-top: 10px;
  margin-left: 10px;
}
